			<CompanyHeader :companySwiper="companySwiper" />
			<div style="height: 8px; background: #eee;"></div>

			<div class="server-body-items">
				<div class="server-body-item" v-for="item in items" @click="changeItem(item)">
					<img :src="item.headimg">

					<div class="img-info">
						<h3>{{item.name}}</h3>
						<div class="item-info">{{item.content}}</div>
						<div class="time">{{item.dateTime}}</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	import _ from 'lodash'
	import CompanyBar from '@/components/CompanyBar.vue'
	import CompanyHeader from '@/components/CompanyHeader.vue'
	
	// 模拟数据
	import Server from '../data-mock/server.json'

	export default {
		name: 'server',
		props: ['from'],
		components: { CompanyBar, CompanyHeader },
		data() {
			return {
				companySwiper: {
					titleName: '服务案例',
					titleInfo: '权威、专业、全面的医疗咨询服务',
					className: 'server'
				},
				tabs: ["精密体验", "血液净化", "干细胞抗衰"],
				tabIndex: 0,
				items: Server.data.list,
				scrollNext: true,
				returnRes: {}
			}
		},
		mounted() {
			
		},
		methods: {
			onInfinite() {
				// 到底部时加载新数据, 需要根据当前 tabIndex
				if (this.scrollNext) {
					fnTiyan(this, {});
				}else{
					if (this.$refs.scroller) this.$refs.scroller.finishInfinite();
				}
			},
			changeItem(item) {
				this.$router.push({ name: 'server-detail', path: 'server-detail', query: { id: item.id } })
			}
		}
	}

	function fnTiyan(that, params) {
		axios.get(
			that.$api.tiyan + qs.stringify(params),
			{
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				timeout: 300000
			}
		).then(response => {
			let result = response.data;

			if (result.code === 0) {
				that.items = result.data.list;
				// if (that.items.length == 0) {
				// 	that.items = result.data.list;
				// }else{
				// 	if (that.scrollNext) {
				// 		that.items = _.concat(that.items, result.data.list);
				// 	}
				// }
				// that.scrollNext = result.data.next;

				// setTimeout(() => {
				// 	if (that.$refs.scroller) { that.$refs.scroller.finishInfinite(); }
				// })
			}
		}).catch(error => {
			if (error.response && error.response.data && error.response.data.code) {
				alert(error.response.data.msg)
			}
		});
	}
</script>

